<template>
  <div class="container-md p-5" v-if="currentUser.username == undefined" style="margin-top: 64px;">
    <h2>請先登入您的帳號，才能夠獲取您的個人資料。</h2>
  </div>
  <div class="container-md p-5" v-else style="margin-top: 58px;">
    <h2>以下是您的個人檔案：</h2>
    <p><strong>姓名：</strong>{{ currentUser.username }}</p>
    <p><strong>您的用戶ID：</strong>{{ currentUser._id }}</p>
    <p><strong>您註冊的電子信箱：</strong>{{ currentUser.email }}</p>
    <p><strong>身份：</strong>{{ currentUser.role }}</p>
  </div>
</template>

<script>
import { mapState } from 'pinia'
import currentUserStore from '@/stores/currentUserStore'

export default {
  computed: {
    ...mapState(currentUserStore, ['currentUser'])
  }
}
</script>
